body {
  margin: 0;
}

.calendar {
  grid-template-columns: repeat(7, 1fr);
  gap: 1px;
  max-width: 350px;
  margin: 0 auto;
  padding: 10px;
  display: grid;
}

.calendar__cell {
  background-color: #eee;
  border: 1px solid #000;
  width: 50px;
  height: 50px;
  transition: transform .5s;
  position: relative;
}

.calendar__cell:hover {
  cursor: pointer;
  background-color: #ffbfcb;
  transform: translateY(20px);
}

.calendar .calendar__cell:first-child:before {
  content: "1";
  font-family: Arial, sans-serif;
  font-size: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

